import appRoutes from "shared/constants/appRoutes";
import { getLastPartInTitleCase } from "shared/helpers/string";
import clockIcon from "shared/images/clock.svg";
import timeZoneIcon from "shared/images/timezone.svg";

const subPagesMap = new Map([
  [
    appRoutes.toolsTimeWorldClock.path,
    {
      name: getLastPartInTitleCase(appRoutes.toolsTimeWorldClock.path, "/"),
      icon: clockIcon,
      description: appRoutes.toolsTimeWorldClock.title
    }
  ],
  [
    appRoutes.toolsTimeTimeZone.path,
    {
      name: getLastPartInTitleCase(appRoutes.toolsTimeTimeZone.path, "/"),
      icon: timeZoneIcon,
      description: appRoutes.toolsTimeTimeZone.description
    }
  ],
]);

const toolsTimeConstants = {
  subPagesMap
};

export default toolsTimeConstants;
