import React from "react";
import { Outlet } from "react-router-dom";

import Tools from "shared/components/tools";
import toolsTimeConstants from "./constants";

function Time() {
  return (
    <>
      <Tools listMap={toolsTimeConstants.subPagesMap} />
      {/* Outlet for child routes */}
      <Outlet />
    </>
  );
}

export default Time;
